
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.wrap{
  width: 100%;
  height: 100%;
  overflow:auto;
}
.wrap-left{
  float: left;
  width: 200px;
  height: calc(100vh);
}
.wrap-right{
   float: left;
  width: calc(100vw - 200px);
  background: #fff;
height:100%;
}
.wrap-right>div{
  width: 100%;
}
.wrap-right .header{
  height: 40px;
}
.wrap-right .content{
  height: calc(100vh - 90px);
}
.wrap-right .footer{
  height: 40px;
  color:black;
  background: #fff;
}



